class ValidForm {
  constructor() {
    // 必须是字母开头， 4-8个字符
    this.usernameRge = /^[A-Za-z][A-Za-z0-9_]{3,7}$/;

    this.passwordRge = /(?=.*([a-zA-Z].*))(?=.*[0-9].*)[a-zA-Z0-9-*/+.~@#$%^&*()]{6,18}$/

  }

  // 验证用户名
  validUsername(value) {
    return this.usernameRge.test(value);
  }

  // 验证密码
  vaildPassword(value) {
    return this.passwordRge.test(value)
  }

  // 验证确认密码
  vaildPwd(v1, v2) {
    return v1 === v2;
  }
}

export const validForm = new ValidForm();
