<template>
  <div class="login">
    <div class="app-logo">
      <div class="logo">
        <img :src="pho.src" alt />
      </div>
    </div>
    <div class="register-input">
      <van-cell-group>
        <van-field
          left-icon="manager"
          v-model="user.value"
          type="text"
          label="用户名"
          placeholder="请输入用户名"
          :error-message="user.errorMessage"
          @input="validUsername(user.value)"
        />
        <van-field
          v-model="pwd1.value"
          :type="pwd1.type"
          left-icon="lock"
          :right-icon="pwd1.icon"
          label="密码"
          placeholder="请输入密码"
          :error-message="pwd1.errorMessage"
          @click-right-icon="checkPassword('pwd1')"
          @input="vaildPassword(pwd1.value)"
        />
      </van-cell-group>
    </div>
    <div class="register-form">
      <van-button
        class="register-btn"
        :loading="isLoading"
        type="primary"
        block
        loading-text="登录中..."
        text="登录"
        @click="loging"
        :disabled="!isValid"
      />
    </div>
    <div class="login">
      <router-link class="login-t" to="/register">没有有账户？注册</router-link>
    </div>
  </div>
</template>

<script>
import { validForm } from "../../tool/validForm";

import { createNamespacedHelpers } from "vuex";

// 基于registerModule的命名空间导入mapState、mapMutations 铺助函数

const { mapState, mapMutations } = createNamespacedHelpers("loginModule");

export default {
  name: "login",

  // 计算属性
  computed: {
    ...mapState([
      "pwd1",
      "isLoading",
      "user",
      "errorMessage",
      "value",
      "isValid",
      "pho"
    ])
  },

  // 方法
  methods: {
    ...mapMutations(["checkPassword"]),
    //  登录按钮
    loging() {
      this.$store.commit("loginModule/login", true);

      let randomT = Math.random() * 3 * 1000;

      setTimeout(() => {
        let users = localStorage.getItem("users");

        users = users ? JSON.parse(users) : [];

        if (users.length === 0) {
          this.$toast({
            duration: 2000,
            message: "该用户不存在"
          });
          this.$store.commit("loginModule/login", false);
          return;
        }

        // 查找用户
        for (let i = 0; i < users.length; i++) {
          if (users[i].username == this.user.value) {
            // 该用户存在
            // 匹配密码
            if (users[i].pwd == this.pwd1.value) {
              //   console.log("登录成功");
              this.$toast({
                duration: 2000,
                message: "登录成功"
              });
              this.$store.commit("loginModule/login", false);

              //跳转到首页
              //记录用户登录状态
              let user = {
                isLogin: true,
                username: this.user.value
              };

              localStorage.setItem("user", JSON.stringify(user));

              return this.$router.push({ name: "home" });
            } else {
              this.$toast({
                duration: 2000,
                message: "用户名或者密码不正确"
              });
              this.$store.commit("loginModule/login", false);
              return;
            }
          }
        }
        this.$toast({
          duration: 2000,
          message: "该用户不存在"
        });
        this.$store.commit("loginModule/login", false);
      }, randomT);
    },

    // 输入表单控件 用户
    validUsername(value) {
      // console.log("valueU ==>> ", value);

      let isValid = validForm.validUsername(value);

      this.$store.commit("loginModule/validUsername", isValid);

      this.$store.commit("loginModule/passForm");
    },

    //验证密码
    vaildPassword(value) {
      // console.log("valueP ==>> ", value);

      let isValid = validForm.vaildPassword(value);

      this.$store.commit("loginModule/vaildPassword", isValid);

      this.$store.commit("loginModule/passForm");
    }
  }
};
</script>
<style >
.login .van-cell-group {
  background: transparent;
}
.login .van-field__control::-webkit-input-placeholder {
  color: white;
}
</style>
<style lang="less" scoped>
.login {
  // height: 100%;
  .app-logo {
    height: 180px;
    position: relative;
    // background: url('../../assets/image/2.jpg') no-repeat center center ;
    background-size: cover;
    .logo {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      background-size: cover;
      width: 80px;
      height: 80px;
      border-radius: 50%;
      box-shadow: 0 0 0 5px rgba(255, 255, 255, 0.35);
      // overflow: hidden;

      // 没有宽高， 要居中
      // top: 50% ;
      // left: 50% ;
      // transform: translate(-50%,-50% )
    }
  }

  .register-input {
    margin-top: 25px;
  }

  .van-field {
    // margin: 15px 0;
    font-size: 16px;
    color: #e4393e;
    font-weight: bold;
    box-shadow: 0px -4.5px 3.5px 0px rgba(255, 255, 255, 0.808);
    padding: 15px;
    background: transparent;
  }

  .register-form {
    padding: 10px;
  }

  .register-btn {
    margin-top: 20px;
  }

  .login {
    text-align: center;
    color: #e4393e;
    font-size: 15px;
    font-weight: bold;
  }
  .login-t {
    color: #e4393e;
    font-weight: bold;
  }
}
</style>